import { createClient } from '@supabase/supabase-js';
import { Platform } from 'react-native';
import { SUPABASE_URL, SUPABASE_ANON_KEY } from '../config/env';
import { getEnvironmentConfig } from '../config/environment';

// Get the site URL based on the environment
const getSiteUrl = () => {
  if (Platform.OS !== 'web') return undefined;
  
  const { webUrl } = getEnvironmentConfig();
  const hostname = window.location.hostname;
  if (hostname === 'localhost' || hostname.includes('127.0.0.1')) {
    return webUrl;
  }
  return window.location.origin;
};

// Custom fetch with timeout and performance logging
const fetchWithTimeout = async (
  input: RequestInfo | URL,
  options: RequestInit = {}
): Promise<Response> => {
  const startTime = Date.now();
  const controller = new AbortController();
  const timeoutId = setTimeout(() => controller.abort(), 10000); // 10 second timeout

  try {
    const response = await fetch(input, {
      ...options,
      signal: controller.signal,
    });

    const duration = Date.now() - startTime;
    if (duration > 1000) {
      const url = typeof input === 'string' ? input : input.toString();
      const method = options.method || 'GET';
      
      // Log slow requests to help with debugging
      console.warn(
        `Slow Supabase request (${duration}ms):`,
        `${method} ${url}`,
        {
          duration,
          method,
          url: url.split('?')[0], // Remove query params for cleaner logs
          status: response.status,
        }
      );
    }

    return response;
  } catch (error) {
    const duration = Date.now() - startTime;
    console.error(
      `Supabase request failed (${duration}ms):`,
      {
        url: typeof input === 'string' ? input : input.toString(),
        method: options.method || 'GET',
        error: error instanceof Error ? error.message : 'Unknown error',
      }
    );
    throw error;
  } finally {
    clearTimeout(timeoutId);
  }
};

// Initialize the Supabase client
export const supabase = createClient(SUPABASE_URL, SUPABASE_ANON_KEY, {
  auth: {
    flowType: 'pkce',
    detectSessionInUrl: true,
    autoRefreshToken: true,
    persistSession: true,
    storage: typeof window !== 'undefined' ? window.localStorage : {
      getItem: () => null,
      setItem: () => {},
      removeItem: () => {},
    },
    storageKey: 'sb-auth-token',
    ...(Platform.OS === 'web' && {
      redirectTo: getSiteUrl(),
      cookieOptions: {
        name: 'sb-auth-token',
        lifetime: 60 * 60 * 24 * 365, // 1 year in seconds
        domain: typeof window !== 'undefined' ? window.location.hostname : undefined,
        sameSite: 'lax',
        secure: window?.location?.protocol === 'https:',
        path: '/',
        httpOnly: false
      }
    }),
  },
  global: {
    headers: {
      'X-Client-Info': 'supabase-js-web'
    },
    fetch: fetchWithTimeout,
  },
  // Add retry configuration for better network resilience
  realtime: {
    params: {
      eventsPerSecond: 2
    }
  },
  db: {
    schema: 'public'
  }
}); 