import { useState, useEffect, useCallback } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Platform } from 'react-native';
import { supabase } from '../utils/supabase';
import { Quiver, CreateQuiverInput, UpdateQuiverInput } from '../types/quiver';
import { BaseProduct } from '../types/scraper';
import { generateFoilId, generateUUID } from '../utils/foilUtils';
import { logger } from '../utils/logger';
import { Environment } from '../config/environment';

const STORAGE_KEYS = {
  QUIVERS: '@foilquiver/quivers',
} as const;

// Get the current environment
const getEnvironment = (): 'dev' | 'staging' | 'prod' => {
  if (process.env.NODE_ENV === 'development') return 'dev';
  if (window.location.hostname.includes('staging')) return 'staging';
  return 'prod';
};

// Get the table name for the current environment
const getTableName = () => {
  const env = getEnvironment();
  return `${env}_quivers`;
};

// Storage helper that works for both web and native
const storage = {
  getItem: async (key: string): Promise<string | null> => {
    if (Platform.OS === 'web') {
      return localStorage.getItem(key);
    }
    return AsyncStorage.getItem(key);
  },
  setItem: async (key: string, value: string): Promise<void> => {
    if (Platform.OS === 'web') {
      localStorage.setItem(key, value);
      return;
    }
    return AsyncStorage.setItem(key, value);
  },
  removeItem: async (key: string): Promise<void> => {
    if (Platform.OS === 'web') {
      localStorage.removeItem(key);
      return;
    }
    return AsyncStorage.removeItem(key);
  }
};

// Local interface for creating quivers
interface LocalCreateQuiverInput {
    name: string;
    description?: string;
    foils: BaseProduct[];
    is_public?: boolean;
}

// Local interface for syncing quivers
interface LocalQuiverData {
    name: string;
    description?: string;
    foils: BaseProduct[];
    is_public?: boolean;
}

const ENV: Environment = process.env.EXPO_PUBLIC_ENV as Environment || 'development';

// Cache for quivers data
const CACHE_DURATION = 60000; // 1 minute
let quiversCache: {
  data: Quiver[] | null;
  timestamp: number;
} = {
  data: null,
  timestamp: 0
};

export const useQuivers = () => {
  const [quivers, setQuivers] = useState<Quiver[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const loadQuivers = useCallback(async (bypassCache = false) => {
    try {
      setIsLoading(true);
      setError(null);

      // Check cache first
      const now = Date.now();
      if (!bypassCache && quiversCache.data && (now - quiversCache.timestamp) < CACHE_DURATION) {
        setQuivers(quiversCache.data);
        setIsLoading(false);
        return;
      }

      // Get current user session
      const { data: { session } } = await supabase.auth.getSession();

      let loadedQuivers: Quiver[] = [];

      if (session?.user) {
        // Load from Supabase if logged in
        const { data, error: fetchError } = await supabase
          .from(getTableName())
          .select('*')
          .or(`user_id.eq.${session.user.id},is_public.eq.true`)
          .order('updated_at', { ascending: false });

        if (fetchError) throw fetchError;
        loadedQuivers = data || [];
      } else {
        // Load from local storage if not logged in
        const storedQuivers = await storage.getItem(STORAGE_KEYS.QUIVERS);
        if (storedQuivers) {
          loadedQuivers = JSON.parse(storedQuivers);
        }
      }

      // Update cache
      quiversCache = {
        data: loadedQuivers,
        timestamp: now
      };

      setQuivers(loadedQuivers);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to load quivers';
      setError(errorMessage);
      console.error('Error loading quivers:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  const reloadQuivers = useCallback(() => loadQuivers(true), [loadQuivers]);

  const createQuiver = useCallback(async ({ 
    name, 
    description, 
    foils = [],
    is_public = false 
  }: { 
    name: string; 
    description?: string; 
    foils?: BaseProduct[];
    is_public?: boolean;
  }) => {
    try {
      setError(null);
      const { data: { session } } = await supabase.auth.getSession();
      const foil_ids = foils.map(foil => generateFoilId(foil));

      if (session?.user) {
        // Create in Supabase if logged in
        const { data, error: insertError } = await supabase
          .from(getTableName())
          .insert([
            {
              name,
              description,
              foil_ids,
              user_id: session.user.id,
              is_public,
              environment: getEnvironment()
            },
          ])
          .select()
          .single();

        if (insertError) throw insertError;
        
        // Invalidate cache and reload
        quiversCache.timestamp = 0;
        await reloadQuivers();
        return data;
      } else {
        // Create in local storage if not logged in
        const newQuiver: Quiver = {
          id: generateUUID(),
          name,
          description: description || '',
          foil_ids,
          created_at: new Date().toISOString(),
          updated_at: new Date().toISOString(),
          is_public,
          user_id: 'local',
          environment: 'local'
        };

        const existingQuivers = await storage.getItem(STORAGE_KEYS.QUIVERS);
        const parsedQuivers: Quiver[] = existingQuivers ? JSON.parse(existingQuivers) : [];
        const updatedQuivers = [newQuiver, ...parsedQuivers];

        await storage.setItem(STORAGE_KEYS.QUIVERS, JSON.stringify(updatedQuivers));
        
        // Invalidate cache and reload
        quiversCache.timestamp = 0;
        await reloadQuivers();
        return newQuiver;
      }
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to create quiver';
      setError(errorMessage);
      console.error('Error creating quiver:', err);
      return null;
    }
  }, [reloadQuivers]);

  const updateQuiver = useCallback(async (quiverId: string, updates: Partial<Quiver>) => {
    try {
      setError(null);
      const { data: { session } } = await supabase.auth.getSession();

      if (session?.user) {
        // Update in Supabase if logged in
        const { error: updateError } = await supabase
          .from(getTableName())
          .update({
            ...updates,
            environment: getEnvironment()
          })
          .eq('id', quiverId)
          .eq('user_id', session.user.id);

        if (updateError) throw updateError;
      } else {
        // Update in local storage if not logged in
        const storedQuivers = await storage.getItem(STORAGE_KEYS.QUIVERS);
        if (!storedQuivers) return false;

        const parsedQuivers: Quiver[] = JSON.parse(storedQuivers);
        const updatedQuivers = parsedQuivers.map(quiver => {
          if (quiver.id === quiverId) {
            return {
              ...quiver,
              ...updates,
              updated_at: new Date().toISOString()
            };
          }
          return quiver;
        });

        await storage.setItem(STORAGE_KEYS.QUIVERS, JSON.stringify(updatedQuivers));
      }

      // Invalidate cache and reload
      quiversCache.timestamp = 0;
      await reloadQuivers();
      return true;
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to update quiver';
      setError(errorMessage);
      console.error('Error updating quiver:', err);
      return false;
    }
  }, [reloadQuivers]);

  const deleteQuiver = useCallback(async (quiverId: string) => {
    try {
      setError(null);
      const { data: { session } } = await supabase.auth.getSession();

      if (session?.user) {
        // Delete from Supabase if logged in
        const { error: deleteError } = await supabase
          .from(getTableName())
          .delete()
          .eq('id', quiverId)
          .eq('user_id', session.user.id);

        if (deleteError) throw deleteError;
      } else {
        // Delete from local storage if not logged in
        const storedQuivers = await storage.getItem(STORAGE_KEYS.QUIVERS);
        if (!storedQuivers) return false;

        const parsedQuivers: Quiver[] = JSON.parse(storedQuivers);
        const updatedQuivers = parsedQuivers.filter(quiver => quiver.id !== quiverId);
        await storage.setItem(STORAGE_KEYS.QUIVERS, JSON.stringify(updatedQuivers));
      }

      // Invalidate cache and reload
      quiversCache.timestamp = 0;
      await reloadQuivers();
      return true;
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to delete quiver';
      setError(errorMessage);
      console.error('Error deleting quiver:', err);
      return false;
    }
  }, [reloadQuivers]);

  // Load quivers on mount
  useEffect(() => {
    loadQuivers();
  }, [loadQuivers]);

  return {
    quivers,
    isLoading,
    error,
    createQuiver,
    updateQuiver,
    deleteQuiver,
    reloadQuivers,
  };
}; 